    // allCheck() {
    // if (this.user.collection.length === this.list.length) {
    //   this.checkedB === true;
    // } else this.checkedB === false;
    // },
  },
  methods: {
    checA() {
      if ((this.user.collection.length = this.list.length)) {
        this.checkedB = true;
      } else this.checkedB === false;
      // this.checkedB = !this.checkedB;
      console.log(this.checkedB);
    },
    select() {
      this.checkedB = !this.checkedB;
      this.user.collection = [];
      if (this.checkedB === true) {
        for (let i in this.list) {
          this.user.collection.push(this.list[i]);
        }
      } else if (this.checkedB === false) {
        this.user.collection = [];
      }

      // this.user.collection = this.list;
